@import url("https://fonts.googleapis.com/css?family=Roboto:400,700,900i");
[v-cloak] {
  display: none !important; }

* {
  box-sizing: border-box; }

ul, li {
  list-style: none;
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-image: linear-gradient(90deg, #250060 0%, #000 50%, #250060 100%);
  color: #fff;
  font-family: 'Roboto', sans-serif; }

.container {
  display: flex;
  padding: 1.5rem; }

.skill {
  flex: 1 1 33.333%;
  padding: 10px; }

.skill_summary {
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .skill_summary > .summary_title {
    margin: 0;
    margin-bottom: 24px;
    color: #FF0093;
    font-size: 48px;
    font-weight: 900;
    font-style: italic; }
  .skill_summary > .summary_level_desc {
    margin: 8px 0;
    padding: 5px 15px;
    background-color: #8000FF;
    color: #250060;
    font-size: 16px;
    font-weight: 400; }
  .skill_summary > .summary_level {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 36px;
    border-bottom: 4px solid #8000FF; }
  .skill_summary > .summary_img {
    max-width: 90%;
    align-self: flex-end;
    animation: shipmove 2s infinite alternate ease-in-out; }

@keyframes shipmove {
  0% {
    transform: translate(0px, 0px); }
  100% {
    transform: translate(-10px, 10px); } }

.summary_message {
  padding-top: 15px;
  position: relative;
  align-self: stretch; }
  .summary_message > .border {
    width: 100%;
    height: 100%; }
    .summary_message > .border > path {
      fill: #FF0093; }
  .summary_message > .text {
    padding: 20px;
    position: absolute;
    left: 0;
    top: 60%;
    transform: translateY(-50%);
    line-height: 1.8; }
  .summary_message .highline {
    color: #FF0093;
    font-weight: 700; }

.progress {
  display: flex;
  transition: opacity .5s; }
  .progress .progress_icon {
    width: 30%; }
  .progress .progress_lists {
    width: 70%;
    padding-bottom: 20px; }
  .progress.disabled {
    opacity: 0.2;
    pointer-events: none; }

.progress:last-of-type > .progress_icon:before, .progress:last-of-type .progress_icon:after {
  display: none; }

.progress_icon {
  position: relative; }
  .progress_icon > img {
    position: relative;
    max-width: 100%;
    z-index: 5; }
  .progress_icon:before {
    content: '';
    position: absolute;
    display: block;
    width: 48px;
    height: calc(100% - 20px - 20px);
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #35006A; }
  .progress_icon:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px 35px 0 35px;
    border-color: #35006a transparent transparent transparent; }

.progress_lists > .lists_block {
  display: flex;
  padding-bottom: 10px; }

.progress_lists .list {
  width: calc(33.333% - 10px);
  margin: 0 5px;
  display: flex;
  flex-direction: column;
  text-align: center; }
  .progress_lists .list.active a {
    border: 2px solid #FF0093;
    background-color: #6900D1; }
  .progress_lists .list.completed a, .progress_lists .list.completed small {
    color: #8000FF; }
  .progress_lists .list.completed a:before {
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 15px 15px 0;
    border-color: transparent #8000FF transparent transparent; }

.progress_lists i {
  font-size: 13px; }

.progress_lists a {
  position: relative;
  display: block;
  padding: 15px 0;
  border: 2px solid transparent;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  text-align: center;
  transition: background-color .3s, color .3s; }
  .progress_lists a > i {
    font-size: 40px; }

.progress_lists small {
  padding-top: 5px;
  transition: color .3s; }

.skill_detail {
  display: flex;
  flex-direction: column; }

.detail_top {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #FF0093;
  border-bottom: 0;
  background-color: #8000FF; }
  .detail_top .logo > i {
    font-size: 130px; }
  .detail_top .name {
    font-size: 36px;
    font-weight: 700;
    text-align: center; }

.detail_bottom {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #FF0093;
  border-top: 0;
  background-color: #6900D1; }

.unit {
  width: 80%;
  text-align: center; }
  .unit > div {
    padding-bottom: 10px; }
  .unit .logo > i {
    font-size: 45px; }
  .unit .name {
    font-weight: 700; }
  .unit li {
    margin: 5px 0;
    padding: 5px 30px;
    border: 2px solid #fff;
    cursor: pointer;
    transition: .3s; }
    .unit li.completed, .unit li:hover {
      background-color: #FF0093;
      border-color: #FF0093; }
